<template>
  <div>
    <v-card color="#FFFFFF" elevation="6">
      <div style="padding-bottom: 1rem">
        <div class="side_panel_title text--secondary">/</div>
        <v-divider></v-divider>
        <line-chart
          :chart-options="chartOptions"
          :chart-data="chartData"
          :chart-id="chartId"
          :dataset-id-key="datasetIdKey"
          :plugins="plugins"
          :css-classes="cssClasses"
          :styles="styles"
          :width="width"
          :height="height"
        ></line-chart>
        <v-divider id="section_divider"></v-divider>
        <v-simple-table fixed-header height="13rem">
          <template v-slot:default>
            <thead>
              <tr id="table_header">
                <th class="text-left table_header"></th>
                <th class="text-left table_header"></th>
                <th class="text-left table_header"></th>
                <th class="text-left table_header"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in house.houseDealList" :key="index">
                <td class="table_body">
                  {{ item.dealYear }}.{{ item.dealMonth }}.{{ item.dealDay }}
                </td>
                <td class="table_body">{{ item.dealAmount }}</td>
                <td class="table_body">{{ item.area }}&#13217;</td>
                <td class="table_body">{{ item.floor }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </div>
    </v-card>
  </div>
</template>

<script>
import { Line } from "vue-chartjs/legacy";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
} from "chart.js";
ChartJS.register(
  Title,
  Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
);
export default {
  components: { LineChart: Line },
  props: {
    house: Object,
    chartId: {
      type: String,
      default: "line-chart",
    },
    datasetIdKey: {
      type: String,
      default: "label",
    },
    width: {
      type: Number,
      // default: 400,
    },
    height: {
      type: Number,
      default: 200,
    },
    cssClasses: {
      default: "",
      type: String,
    },
    styles: {
      type: Object,
      default: () => {},
    },
    plugins: {
      type: Array,
      default: () => [{}],
    },
  },
  data() {
    return {
      chartOptions: {
        responsive: true,
      },
    };
  },
  computed: {
    chartData() {
      return {
        labels: this.house.avgPriceList.map(({ dealYear }) => {
          return `${dealYear}`;
        }),
        datasets: [
          {
            data: this.house.avgPriceList.map(({ avgPrice }) => {
              return avgPrice / 10000;
            }),
            label: "()",
            borderColor: "rgb(75, 192, 192)",
            backgroundColor: "rgb(75, 192, 192)",
          },
        ],
      };
    },
  },
};
</script>

<style scoped>
.table_header {
  height: 1.5rem !important;
}
.table_body {
  font-size: 0.8rem !important;
}
#section_divider {
  border-width: 0.05rem !important;
}
</style>
